h3 {
  color: #424242;
}

.btn span {
  color: #00BFA5;
}

.btn span:hover {
  color: #004D40;
}

.btn:hover {
  -webkit-animation: btnEnlarge 0.75s;
          animation: btnEnlarge 0.75s;
}

@-webkit-keyframes btnEnlarge {
  0%, 100% {
    -webkit-transform: translate(0,0);
            transform: translate(0,0);
  }
  50% {
    -webkit-transform: scale(1, 1.1);
            transform: scale(1, 1.1);
  }
}

@keyframes btnEnlarge {
  0%, 100% {
    -webkit-transform: translate(0,0);
            transform: translate(0,0);
  }
  50% {
    -webkit-transform: scale(1, 1.1);
            transform: scale(1, 1.1);
  }
}

.card {
  padding: 24px 48px;
}

.project-icon {
  font-size: 40px;
  color: #004D40;
}



